import React, { Component } from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import { Field, reduxForm } from 'redux-form';
import { signIn, unmountAuth } from 'actions/auth';

class Signin extends Component {
  static store(state) {
    return {
      errorMessage: state.auth.error
    };
  }

  componentWillUnmount() {
    const { unmountAuth } = this.props;
    
    unmountAuth();
  }

  renderField = ({ input, label, type, meta: { touched, error } }) => {
    return (
      <div>
        <label>{label}</label>
        <div>
          <input {...input} placeholder={label} type={type} className="form-control" />
          {touched && error && <div className="error">{error}</div>}
        </div>
      </div>
    );
  }

  handleFormSubmit = ({ email, password }) => {
    const { signIn } = this.props;

    signIn({ email, password });
  }

  renderAlert = () => {
    const { errorMessage } = this.props;
    if (errorMessage) {
      return (
        <div className="alert alert-danger">
          <strong><FormattedMessage id="error" /></strong>
          <FormattedMessage id={errorMessage} />
        </div>
      );
    }
    return null;
  }

  render() {
    const { handleSubmit } = this.props;

    return(
      <form onSubmit={handleSubmit(this.handleFormSubmit)}>
        <fieldset className="form-group">
          <Field name="email" type="email" component={this.renderField} label="Email" className="form-control" />
        </fieldset>
        <fieldset className="form-group">
          <Field name="password" type="password" component={this.renderField} label="Password" className="form-control" />
        </fieldset>
        {this.renderAlert()}
        <button action="submit" className="btn btn-primary">Sign in</button>
      </form>
    );
  }
}

module.exports = connect(Signin.store, { signIn, unmountAuth })(reduxForm({
  form: 'signin'
})(Signin));



